}

.header .container .burger .fa-bars {
  font-size: 24px;
}

.header .container .burger-menu {
  opacity: 0;
  transition: all 0.5s;
  position: absolute;
  left: -80px;
  z-index: 10000;
  min-height: 70px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: rgba(0, 0, 0, 0.65);
  box-sizing: border-box;
  border-top: 2px solid #fff200;
  padding: 25px 0 10px 0;
  width: 120px;
}

.header .container .burger-menu li {
  padding-left: 20px;
  padding-bottom: 25px;
}

.header .container .burger-menu li a,
.header .container .burger-menu li .fa-angle-left {
  color: #fdfdfd;
  font-size: 14px;
  letter-spacing: 1.05px;
  text-decoration: none;
}

.header .container .burger-menu li:hover .burger-menu-down-drop {
  display: flex;
}

.header .container .burger-menu li:hover .I-one,
.header .container .burger-menu li .I-two {
  display: none;
}

.header .container .burger-menu li:hover .I-two,
.header .container .burger-menu li:hover .a {
  display: inline-block;
  color: #fff200;
  font-size: 14px;
  letter-spacing: 1.05px;
}

.header .container .burger-menu-down {
  z-index: 1111;
  position: relative;
  display: block;
  box-sizing: border-box;
  padding-right: 28px;
  padding-bottom: 18px;
}

.header .container .burger-menu-down-drop {
  position: absolute;
  left: -160px;
  top: -10px;
  width: 160px;
  display: none;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.65);
  box-sizing: border-box;
  padding: 25px 0 10px 0;
  border-top: 2px solid #fff200;
  justify-content: space-between;
  z-index: 1111;
}

.header .container .burger-menu-down-drop .submenu {
  position: relative;
}

.header .container .burger-menu-down-drop .submenu:hover .first-I-one,
.header .container .burger-menu-down-drop .submenu .first-I-two {
  display: none;
}

.header .container .burger-menu-down-drop .submenu:hover .first-I-two,
.header .container .burger-menu-down-drop .submenu:hover .first-a {
  display: inline-block;
  color: #fff200;
  font-size: 14px;
  letter-spacing: 1.05px;
}

.header .container .burger-menu-down-drop .submenu a,
.header .container .burger-menu-down-drop .submenu .fa-angle-right {
  color: #fdfdfd;
  font-size: 14px;
  letter-spacing: 1.05px;
}

.header .container .burger-menu-down-drop .submenu-down {
  position: absolute;
  left: -160px;
  top: -5px;
  display: block;
  width: 160px;
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, 0.65);
  box-sizing: border-box;
  padding: 25px 0 10px 0;
  border-top: 2px solid #fff200;
  justify-content: space-between;
  z-index: 1111;
}

.header .container .burger-menu-down-drop .submenu-down li .second-I-one,
.header .container .burger-menu-down-drop .submenu-down li .second-I-two {
  position: relative;
}

.header .container .burger-menu-down-drop .submenu-down li:hover .second-I-one,
.header .container .burger-menu-down-drop .submenu-down li .second-I-two {
  display: none;
}

.header .container .burger-menu-down-drop .submenu-down li:hover .second-I-two,
.header .container .burger-menu-down-drop .submenu-down li:hover .second-a {
  display: inline-block;
  color: #fff200;
  font-size: 14px;
  letter-spacing: 1.05px;
}

.header .container .burger .menu-opened {
  opacity: 1;
}

